import styled from "styled-components";
import SearchIcon from "react-icons/lib/fa/search";
import { useState, useRef } from "react";
import { animated, useSpring } from "react-spring";
import DeleteIcon from "react-icons/lib/md/clear";

const placeholder = "";

type Props = {
  onChange: (value: string) => void;
  onFocus?: () => void;
  onBlur?: () => void;
  value?: string;
};

const SearchBox = ({ value, onChange, onFocus, onBlur }: Props) => {
  const [focus, setFocus] = useState(false);
  const [hover, setHover] = useState(false);
  const inputRef = useRef(null);
  /*   
  const props = useSpring({
    maxWidth: focus || hover ? 1000 : 350,
    config: { tension: 500 }
  });
  style={props}
  */
  return (
    <SearchBoxArea
      onMouseEnter={() => {
        setHover(true);
        setFocus(true);
      }}
      onMouseLeave={() => {
        setHover(false);
        setFocus(false);
      }}
      onClick={() => {
        setFocus(true);
        onFocus && onFocus();
        inputRef.current.focus();
      }}
    >
      {/* <Buttons>
      {labels.map(label => <Button key={label}>{label}</Button>)}
    </Buttons> */}
      <SearchIcon
        size={22}
        color="rgba(255, 255, 255, 0.5)"
        style={{ margin: 8 }}
      />
      {/* <CategoryArea>
        <CategoryType>person:</CategoryType>
        <ValueType>Thomas</ValueType>
      </CategoryArea>
      <CategoryArea>
        <CategoryType>programming language:</CategoryType>
        <ValueType>C++</ValueType>
      </CategoryArea> */}

      <input
        style={{
          padding: "6px",
          display: "flex",
          flex: 1,
          borderRadius: "10px",
          backgroundColor: "transparent",
          borderStyle: "solid",
          borderColor: "rgba(255, 255, 255, 0.05)",
          borderWidth: "0px",
          flexFlow: "row wrap",
          margin: "2px",
          outline: "none",
          fontSize: "18px",
          color: "white",
          caretColor: "white",
          overflow: "hidden"
        }}
        ref={inputRef}
        type="text"
        autoCorrect="off"
        placeholder={placeholder}
        onChange={event => onChange(event.target.value)}
        value={value}
        onKeyDown={e => {
          if (e.key === "Enter") {
            e.target.blur();
          }
        }}
        onMouseOver={e => {
          e.target.focus();
        }}
        onFocus={() => {
          setFocus(true);
          onFocus && onFocus();
        }}
        onBlur={() => {
          setFocus(false);
          onBlur && onBlur();
        }}
      />
      {value && (
        <DeleteIcon
          size={22}
          color="rgba(255,255,255,0.5)"
          style={{ marginRight: 10 }}
          onClick={() => onChange("")}
        />
      )}
    </SearchBoxArea>
  );
};

export default SearchBox;

const SearchBoxArea = animated(styled.div`
  padding: 6px 10px;
  display: flex;
  flex: 1 1 450px;
  border-radius: 25px;
  background-color: rgba(255, 255, 255, 0.09);
  border-style: solid;
  border-color: rgba(255, 255, 255, 0.05);
  border-width: 0;
  flex-flow: row wrap;
  align-items: center;
  flex-wrap: nowrap;
  max-width: 1000px;
  margin: 4.5vw 0;
  /* transition: all 0.3s;
  &:hover {
    max-width: 1000px;
  } */
`);

const CategoryArea = animated(styled.div`
  border-radius: 15px;
  border-color: rgba(255, 255, 255, 0);
  /* background-color: hsla(246, 76%, 61%, 0.8); */
  border-style: solid;
  /* box-shadow: 2px 2px 5px rgb(0, 0, 0, 0.1); */

  border-width: 0px;
  flex-flow: row wrap;
  display: flex;
  padding: 0 20px;
  margin: 0 0 0 15px;
  height: 38px;

  align-items: center;
  flex-wrap: nowrap;
  max-width: 1000px;

  overflow: hidden;
`);

const CategoryType = animated(styled.div`
  border-color: rgba(255, 255, 255, 0);
  background-color: transparent;
  border-style: solid;
  /* box-shadow: 2px 2px 5px rgb(0, 0, 0, 0.1); */
  color: black;
  border-width: 0px;
  flex-flow: row wrap;
  display: flex;
  padding: 0 10px 0 20px;
  height: 100%;
  margin-left: 0px;
  color: white;

  align-items: center;
  flex-wrap: nowrap;
  max-width: 1000px;
  &:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
`);

const ValueType = animated(styled.div`
  border-color: rgba(255, 255, 255, 0.1);
  background-color: transparent;
  /* border-left: 1px; */
  border-style: solid;
  border-width: 0;
  /* box-shadow: 2px 2px 5px rgb(0, 0, 0, 0.1); */
  flex-flow: row wrap;
  display: flex;
  padding: 0 20px 0 10px;
  height: 100%;
  margin-right: 0px;
  color: white;

  align-items: center;
  flex-wrap: nowrap;
  max-width: 1000px;
  &:hover {
    background-color: rgba(255, 255, 255, 0.1);
  }
`);



