import { useEffect, useState } from "react";
import styled, { css } from "styled-components";
import { Article } from "../data/articles";
import browserDetect from "browser-detect";

export default ({ data }: { data: Article }) => {
  const [browser, setBrowser] = useState(null);
  useEffect(() => {
    setBrowser(browserDetect().name);
  }, [browser]);

  return (
    <ArticleBox>
      <Subheadline>
        {data.author}
        {data.date ? `, ${data.date}` : ""}
      </Subheadline>
      <a target="_blank" rel="noopener noreferrer" href={data.link}>
        <Headline browser={browser}>{data.headline}</Headline>
      </a>
      {data.image && <Photo src={data.image} />}
      <Body>{data.body}</Body>
      {data.topics && Array.isArray(data.topics) && (
        <Tags>
          {data.topics.map((topic, index) => (
            <Tag key={index}>{topic}</Tag>
          ))}
        </Tags>
      )}
      <Spacer />
      <Separator />
    </ArticleBox>
  );
};

const ArticleBox = styled.div`
  color: rgba(255, 255, 255, 0.9);
  font-size: 18px;
  max-width: 500px;
  margin-top: 3vw;
  display: flex;
  flex-direction: column;

  @media screen and (max-width: 600px) {
    width: 96%;
    padding-left: 2%;
    padding-right: 2%;
  }
`;

const Headline = styled.h2`
  font-size: calc(26px + (28 - 26) * ((100vw - 300px) / (1600 - 300)));
  line-height: 125%;
  color: rgba(255, 255, 255, 1);
  margin-top: 0;
  margin-bottom: 15px;
  transition: 0.2s;
  ${props =>
    props.browser &&
    css`
      letter-spacing: ${props.browser === "safari" ? "-2px" : "0px"};
    `}
`;

const Subheadline = styled.p`
  font-size: 14px;
  color: rgba(255, 255, 255, 0.7);
  margin-bottom: 20px;
`;

const Body = styled.p`
  line-height: 150%;
  margin-top: 30px;
  margin-bottom: 25px;

  ${props =>
    props.browser &&
    css`
      letter-spacing: ${props.browser === "safari" ? "-0.5px" : "0px"};
    `}
`;

const Separator = styled.hr`
  border: 0;
  height: 1px;
  margin: 10px 0;
  background: rgba(255, 255, 255, 0.1);

  @media screen and (max-width: 600px) {
    margin-bottom: 0px;
  }
`;

const Photo = styled.img`
  max-width: 100%;
  max-height: 160px;
  object-fit: contain;
  align-self: flex-start;
  margin-top: 10px;
`;

const Tag = styled.div`
  color: rgba(255, 255, 255, 0.5);
  font-size: 13px;
  background-color: rgba(255, 255, 255, 0.02);
  margin: 3px;
  border: 1px solid rgba(255, 255, 255, 0.05);
  padding: 7px 15px;
  border-radius: 20px;
  &:hover {
    background-color: rgba(255, 255, 255, 0.03);
    border: 1px solid rgba(255, 255, 255, 0.2);
  }
`;

const Tags = styled.div`
  margin-top: 25px;
  display: flex;
  flex-flow: row wrap;
`;

const Spacer = styled.div`
  flex: 1;
  min-height: 25px;
`;



