import styled from "styled-components";
import Link from "next/link";
import { withRouter } from "next/router";
import SearchBox from "./Searchbox";
import NavButtons from "./NavButtons";

const labels = [
  "News",
  "Overview",
  "Community",
  "Roadmap",
  "Getting Started",
  "Docs",
  "Spec",
  "FAQ"
  /* 
  "Events",
  "Apps",
  "Technology",
  "Demos",
  "Apps",
  "Code"
  */
];

type Props = {
  onChange: (value: string) => void;
  experimental?: boolean;
  value?: string;
};

export default withRouter(({ onChange, experimental, value }: Props) => (
  <NavBarArea>
    <Column>
      <Row>
        <Link href="/">
          <Logo alt="WebAssembly logo" src="/static/webassembly-banner.svg" />
        </Link>
        <Column />
        <SearchBox onChange={onChange} value={value} />
      </Row>
      {experimental && (
        <NavRow>
          <NavButtons labels={labels} activeIndex={0} />
        </NavRow>
      )}
    </Column>
  </NavBarArea>
));

export const NavBarArea = styled.div`
  width: 90%;
  margin-top: 3.5%;
  margin-left: 5%;
  margin-right: 5%;
`;

const Logo = styled.img`
  /* border-style: solid; */
  max-width: 450px;
  width: 90%;
`;

const Column = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
`;

const Row = styled.div`
  display: flex;
  flex-flow: row wrap;
  align-items: center;
`;

const NavRow = styled.div`
  display: flex;
  width: 100%;
  flex-flow: row wrap;
  align-items: center;
  justify-content: space-between;
`;

const Spacer = styled.div`
  display: flex;
  flex: 0.1;
  align-items: center;
  justify-content: flex-end;
  font-size: 18px;
  color: rgba(255, 255, 255, 0.5);
  margin-top: 10px;
`;



