import FaLock from "react-icons/lib/fa/lock";

function findWord(word, str) {
  return str.split(" ").some(function(w) {
    return w.startsWith(word);
  });
}

export default () => {
  const [value, setValue] = useState("");
  const articleFilter = ({ headline, author, topics, body }) =>
    (topics && topics.some(topic => topic.toLowerCase().startsWith(value))) ||
    (author && findWord(value, author.toLowerCase())) ||
    (headline && findWord(value, headline.toLowerCase())) ||
    (body && findWord(value, body.toLowerCase()));
  return (
    <Centered>
      <Head>
        <title>WebAssembly</title>
      </Head>
      <NavBar onChange={value => setValue(value.toLowerCase())} experimental />
      <Content />
    </Centered>
  );
};

